body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Roboto, Arial, Helvetica,
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header {
  height: 90px;
  width: 100%;
  background: #1e2028;
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 624px) {
  .header {
    height: 130px;
  }
}
@media screen and (max-width: 500px) {
  .header {
    flex-wrap: wrap;
    height: 140px;
  }
}

.header-text {
  font-size: 36px;
  color: #f4f4f4;
  opacity: 0.95;
  display: block;
  padding-top: 25px;
}

.header-text:hover {
  opacity: 1;
}

.header-logo {
  margin-top: 16px;
  -webkit-animation: App-logo-spin infinite 10s linear;
          animation: App-logo-spin infinite 10s linear;
  height: 50px;
  pointer-events: none;
}

.header-logo-wrapper {
  display: flex;
}
@media screen and (max-width: 500px) {
  .header-logo-wrapper {
    margin-left: 10%;
  }
}

.search-wrapper {
  position: relative;
  display: inline-block;
  margin-top: 15px;
  width: 300px;
  margin-right: 50px;
}

.search-form {
  position: relative;
  margin-top: 14px;
}
@media screen and (max-width: 624px) {
  .search-form {
    margin-top: 30px;
  }
}
@media screen and (max-width: 500px) {
  .search-form {
    margin-top: 0px;
    margin-left: 20%;
  }
}

.search-input {
  width: 100%;
  outline: none;
  -webkit-transition: all 0.2s ease 0s;
  transition: all 0.2s ease 0s;
  padding: 0 10px;
  height: 37px;
  border: 1px solid #424754;
  background: 0 0;
  color: #8e95a5;
  line-height: 32px;
  border-radius: 3px;
  cursor: text;
  font-size: 18px;
  text-align: start;
}

.search-button {
  position: absolute;
  top: 4px;
  right: -17px;
  display: block;
  width: 32px;
  height: 32px;
  border: none;
  background: 0 0;
  color: #646b7f;
  font-size: 1.2rem;
  padding: 0;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.movie-wrapper {
  display: flex;
  list-style-type: none;
  width: 47%;
  height: 200px;
  padding: 15px;
}
@media screen and (max-width: 1045px) {
  .movie-wrapper {
    width: 70%;
    margin: 0 auto;
  }
}

@media screen and (max-width: 679px) {
  .movie-wrapper {
    width: 90%;
  }
}

.movie-wrapper:hover {
  background: #30333f;
  border-radius: 7px;
  -webkit-transition: 200ms ease-in-out;
  transition: 200ms ease-in-out;
}
.movie-wrapper:hover > .movie-poster {
  -webkit-transition: 200ms ease-in-out;
  transition: 200ms ease-in-out;
  opacity: 1;
}
.movie-wrapper:hover > .movie-info {
  -webkit-transition: 200ms ease-in-out;
  transition: 200ms ease-in-out;
  opacity: 1;
}

.movie-poster {
  border-radius: 7px;
  overflow: hidden;
  position: relative;
  display: block;
  height: 200px;
  min-width: 150px;
  opacity: 0.93;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.movie-info {
  margin-left: 15px;
  overflow-y: auto;
  height: 200px;
  opacity: 0.95;
  padding-right: 10px;
}

.movie-info::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #2d3038;
}

.movie-info::-webkit-scrollbar {
  width: 12px;
  background-color: #23252b;
}

.movie-info::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #1e2028;
}

.movie-title {
  font-size: 20px;
  color: #f4f4f4;
  word-wrap: break-word;
  margin-bottom: 12px;
  display: inline-block;
  margin-right: 14px;
}

.movie-rank > span {
  color: #fbb829;
  padding-right: 10px;
}

.movie-rank {
  position: relative;
  display: inline-block;
  margin-bottom: 13px;
}
.movie-rank-number {
  color: #f4f4f4 !important;
  padding-left: 13px;
}

.fa-star {
  position: absolute;
  top: 4px;
  left: 47px;
  font-size: 10px;
  display: inline-block !important;
  color: #fbb829;
}

.movie-overview {
  display: inline-block;
}

.movie-description {
  font-size: 15px;
}

.main-wrapper {
  height: 100%;
  background: #23252b;
  color: white;
  padding-top: 30px;
  min-height: 1000px;
}

.movies-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
  height: 100%;
  padding-left: 15px;
  padding-right: 15px;
}

.loading {
  text-align: center;
  font-size: 30px;
  color: #646b7f;
}

